.gallery {
  padding: 60px 0;
}

.movies,
.movies-lib {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  justify-content: center;

  @media screen and (min-width: $tablet) {
    justify-content: center;
  }

  @media screen and (min-width: $desktop) {
    justify-content: flex-start;
  }
}

.movie_list_item {
  position: relative;
  width: 280px;
  font-size: 12px;
  transition: scale 250ms;
  cursor: pointer;

  @media screen and (min-width: $tablet) {
    width: 336px;
    font-size: 12px;
  }

  @media screen and (min-width: $desktop) {
    flex-basis: calc((100% - 32px) / 3);
    font-size: 20px;
  }
}

// .movie_list_item:hover {
//   scale: 102%;
// }

.movie__cover-inner {
  position: relative;
  height: 360px;
}

.movie__cover {
  max-width: 100%;
  height: 100%;
}

.movie-info {
  padding: 10px 0px;
}

.movie-title {
  color: black;
  font-weight: 500;
}

.movie-date {
  color: #ff6b08;
  font-weight: 500;
}

.movie__average {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;

  color: #ffffff;
  font-size: 14px;
}

