.watched-warning {
  color: #000;
  font-size: 18px;
  line-height: 1.2;
  max-width: 250px;
  margin: 0 auto;
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    font-size: 20px;
    line-height: 1.5;
    max-width: 450px;
  }

  @media screen and (min-width: $desktop) {
    font-size: 25px;
    line-height: 1.6;
    max-width: 550px;
  }
}

.movie_list_image {
  border-radius: 5px;

  @media screen and (min-width: $desktop) {
    height: 592.5px;
  }
}

.meme-notif-nothing {
  display: none;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}
