button {
  cursor: pointer;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
}
.page-btn {
  min-width: 40px;
  height: 40px;
  padding: 8px;

  font-weight: 500;
  font-family: inherit;
  font-size: 12px;
  line-height: 1.25;
  border-radius: 5px;
  border: none;

  background: transparent;
  border: none;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:not(:last-child) {
    margin-right: 2px;
  }
  &:hover,
  &:focus {
    color: #fff;
    background-color: rgba($accent-color, 0.6);
  }
}

.page-btn.active-page {
  background-color: $accent-color;
  color: $primary-text-color;
}
.prev-btn,
.next-btn {
  display: flex;
  justify-content: center;
  align-content: center;

  width: 40px;
  height: 40px;

  border-radius: 5px;
  border: none;

  background: $secondary-bgc;
}
.icon {
  display: flex;
  align-self: center;

  width: 16px;
  height: 16px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  pointer-events: none;
}
